import { Component, OnInit, AfterViewInit } from '@angular/core';
import { topAppBar, notchedOutline, textField, tabs, drawer } from 'material-components-web';

@Component({
  selector: 'app-tabs',
  templateUrl: './tabs.component.pug',
  styleUrls: ['./tabs.component.styl']
})
export class TabsComponent implements OnInit, AfterViewInit {

  ar: Array<any> = new Array(3).fill(0)
  selectedTab: number = -1
  constructor() { }

  ngAfterViewInit () {
    const MDCTabBar = tabs.MDCTabBar
    var dynamicTabBar = window['dynamicTabBar'] = new MDCTabBar(document.querySelector('#dynamic-tab-bar'));
    var dots = document.querySelector('.dots');
    var panels = document.querySelector('.panels');

    dynamicTabBar.tabs.forEach(function(tab) {
      tab.preventDefaultOnClick = true;
    });


    dynamicTabBar.listen('MDCTabBar:change', function ({detail: tabs}) {
      this.selectedTab = tabs.activeTabIndex
    });

    dots.addEventListener('click', function (evt) {
      if (!evt.target['classList'].contains('dot')) {
        return;
      }

      evt.preventDefault();

      var dotIndex = [].slice.call(dots.querySelectorAll('.dot')).indexOf(evt.target);
      this.selectedTab = dotIndex
    })

  }

  ngOnInit() {
  }

}



