import { Component, OnInit, AfterViewInit } from '@angular/core';
import { topAppBar, notchedOutline, textField, tabs, drawer } from 'material-components-web';
import * as mdc from 'material-components-web';

@Component({
  selector: 'app-drawer',
  templateUrl: './drawer.component.pug',
  styleUrls: ['./drawer.component.styl']
})
export class DrawerComponent implements OnInit, AfterViewInit {

  constructor() { }


  ngOnInit() {}

  ngAfterViewInit () {
    console.log(mdc)
    const MDCTemporaryDrawer = drawer.MDCTemporaryDrawer
    window['_drawer'] = new MDCTemporaryDrawer(document.querySelector('.mdc-drawer--temporary'));
    // document.querySelector('.menu').addEventListener('click', () => _drawer.open = true);
  }

  openDrawer() {
    window['_drawer'].open != window['_drawer'].open
  }

}



